.car-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #0b1723;
    color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.1);
    overflow: hidden;
    max-width: 100%;
    padding: 20px;
    margin: 0 auto;
    /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –∞–≤—Ç–æ–º–æ–±–∏–ª—è */
.car-details img {
    width: 100%;
    /* –ó–∞–Ω–∏–º–∞–µ—Ç –≤—Å—é —à–∏—Ä–∏–Ω—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
    max-width: 400px;
    /* –û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ —à–∏—Ä–∏–Ω—ã –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é */
    height: auto;
    /* –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –ø—Ä–æ–ø–æ—Ä—Ü–∏–π */
    border-radius: 10px;
    border: 2px solid #ffffff33;
    margin-bottom: 20px;
    object-fit: cover;
    /* –û–±—Ä–µ–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –¥–ª—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –ø—Ä–æ–ø–æ—Ä—Ü–∏–π */
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ */
.car-details .info {
    width: 100%;
    text-align: center;
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ –∞–≤—Ç–æ–º–æ–±–∏–ª—è */
.car-title {
    font-size: 24px;
    margin-bottom: 20px;
    color: #f39c12;
}

/* –¢–µ–∫—Å—Ç –∞–≤—Ç–æ–º–æ–±–∏–ª—è */
.car-text {
    color: #bad3eb;
    margin: 10px 0;
    font-size: 16px;
}

.light-theme .car-text {
    color: black;
}
/* Turbo –≤—ã–¥–µ–ª–µ–Ω–∏–µ */
.car-details .turbo {
    color: #e74c3c;
    font-weight: bold;
}

/* –ö–Ω–æ–ø–∫–∞ –¥–µ—Ç–∞–ª–µ–π */
.details-button {
    background-color: #f39c12;
    color: #ffffff;
    font-size: 16px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    margin-top: 20px;
    min-width: 130px;
    text-align: center;
}

.details-button:hover {
    background-color: #e67e22;
    transform: scale(1.05);
}

.details-button:active {
    background-color: #d35400;
    transform: scale(0.98);
}

/* –§–æ—Ä–º–∞ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∞–≤—Ç–æ–º–æ–±–∏–ª—è */
.add-car-form {
    background-color: #2c2c2c;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.1);
    max-width: 400px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.add-car-form input,
.add-car-form select {
    background-color: #333;
    color: #ffffff;
    border: 1px solid #555;
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
}

.add-car-form input:focus,
.add-car-form select:focus {
    border-color: #f39c12;
    outline: none;
}

.add-car-form button {
    background-color: #f39c12;
    color: #000;
    font-size: 18px;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.add-car-form button:hover {
    background-color: #e67e22;
}

/* –°–ø–∏—Å–æ–∫ –ø–æ–¥—Å–∫–∞–∑–æ–∫ */
.suggestions {
    background-color: #333;
    color: #ffffff;
    border: 1px solid #555;
    padding: 5px;
    border-radius: 5px;
    margin-top: -10px;
    max-height: 150px;
    overflow-y: auto;
}

.suggestions li {
    padding: 5px;
    cursor: pointer;
    transition: background 0.3s;
}

.suggestions li:hover {
    background-color: #444;
}

/* –ß–µ–∫–±–æ–∫—Å Turbo */
.turbo-checkbox {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
    color: #ffffff;
}

.turbo-checkbox input {
    width: 20px;
    height: 20px;
}

/* –ú–µ–¥–∏–∞-–∑–∞–ø—Ä–æ—Å—ã –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤ */
@media (max-width: 768px) {
    .car-details {
        padding: 15px;
        /* –£–º–µ–Ω—å—à–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã */
    }

    .car-details img {
        max-width: 100%;
        /* –ü–æ–ª–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö */
        max-height: 250px;
        /* –û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –≤—ã—Å–æ—Ç—ã –¥–ª—è –∫–æ–º–ø–∞–∫—Ç–Ω–æ—Å—Ç–∏ */
    }

    .car-title {
        font-size: 20px;
        /* –£–º–µ–Ω—å—à–∞–µ–º –∑–∞–≥–æ–ª–æ–≤–æ–∫ */
        margin-bottom: 15px;
    }

    .car-text {
        font-size: 14px;
        /* –£–º–µ–Ω—å—à–∞–µ–º —Ç–µ–∫—Å—Ç */
    }

    .details-button {
        font-size: 14px;
        padding: 8px 16px;
        margin-top: 15px;
    }

    .add-car-form {
        padding: 15px;
        max-width: 100%;
        /* –ü–æ–ª–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö */
    }
}

@media (max-width: 480px) {
    .car-details {
        padding: 10px;
    }

    .car-details img {
        max-height: 200px;
        /* –ï—â–µ –º–µ–Ω—å—à–µ –¥–ª—è –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤ */
    }

    .car-title {
        font-size: 18px;
    }

    .car-text {
        font-size: 12px;
    }

    .details-button {
        font-size: 12px;
        padding: 6px 12px;
    }

    .add-car-form input,
    .add-car-form select {
        font-size: 14px;
        padding: 8px;
    }

    .add-car-form button {
        font-size: 16px;
        padding: 8px;
    }
}

@media (min-width: 768px) {
    .car-details img {
        max-width: 500px !important;
        height: auto;
        /* –£–±–∏—Ä–∞–µ–º —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω—É—é –≤—ã—Å–æ—Ç—É */
        object-fit: contain;
        /* –ü–æ–∫–∞–∑—ã–≤–∞–µ–º –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Ü–µ–ª–∏–∫–æ–º */
    }
}

/* CarDetails.css */

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ —Å–ø–∏–Ω–Ω–µ—Ä–∞ */
.spinner-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    /* –£–±–µ–¥–∏—Ç–µ—Å—å, —á—Ç–æ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∑–∞–Ω–∏–º–∞–µ—Ç –º–µ—Å—Ç–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è */
}

/* –°—Ç–∏–ª–∏ —Å–ø–∏–Ω–Ω–µ—Ä–∞ */
.spinner {
    width: 40px;
    height: 40px;
    border: 4px solid #f3f3f3;
    /* –°–≤–µ—Ç–ª–æ-—Å–µ—Ä—ã–π —Ñ–æ–Ω */
    border-top: 4px solid #3498db;
    /* –°–∏–Ω–∏–π —Ü–≤–µ—Ç –¥–ª—è –≤—Ä–∞—â–µ–Ω–∏—è */
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –≤—Ä–∞—â–µ–Ω–∏—è */
@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}