.progress-bar-container {
    width: 100%;
    height: 24px;
    /* –£–≤–µ–ª–∏—á–∏–º –≤—ã—Å–æ—Ç—É –¥–ª—è –±–æ–ª–µ–µ –≤—ã—Ä–∞–∑–∏—Ç–µ–ª—å–Ω–æ–≥–æ –≤–∏–¥–∞ */
    background: #f3eeee;
    /* –¢–µ–º–Ω—ã–π —Ñ–æ–Ω, –∫–∞–∫ —É –∫—Ä—É–≥–æ–≤–æ–≥–æ */
    border-radius: 12px;
    /* –ë–æ–ª–µ–µ –ø–ª–∞–≤–Ω—ã–µ —É–≥–ª—ã */
    overflow: hidden;
    position: relative;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5), inset 0 0 5px rgba(0, 0, 0, 0.3);
    /* –¢–µ–Ω–∏ —Å–Ω–∞—Ä—É–∂–∏ –∏ –≤–Ω—É—Ç—Ä–∏ */
    padding: 2px;
    border: 1px solid wheat;
    /* –í–Ω—É—Ç—Ä–µ–Ω–Ω–∏–π –æ—Ç—Å—Ç—É–ø –¥–ª—è –∫–æ–Ω—Ç—Ä–∞—Å—Ç–∞ */
}

.progress-bar {
    height: 100% !important;
    transition: width 0.6s ease-in-out;
    /* –ü–ª–∞–≤–Ω–∞—è –∞–Ω–∏–º–∞—Ü–∏—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è */
    background: linear-gradient(135deg, #4caf50, #81c784);
    /* –ó–µ–ª–µ–Ω—ã–π –≥—Ä–∞–¥–∏–µ–Ω—Ç –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é */
    border-radius: 10px;
    /* –ó–∞–∫—Ä—É–≥–ª–µ–Ω–Ω—ã–µ —É–≥–ª—ã –≤–Ω—É—Ç—Ä–∏ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
    filter: drop-shadow(0 0 5px rgba(76, 175, 80, 0.5));
    /* –°–≤–µ—á–µ–Ω–∏–µ */
   
}

/* –í–∞—Ä–∏–∞–Ω—Ç—ã –≥—Ä–∞–¥–∏–µ–Ω—Ç–æ–≤ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è */
.progress-bar.low {
    background: linear-gradient(135deg, #f44336, #ef5350);
    /* –ö—Ä–∞—Å–Ω—ã–π –≥—Ä–∞–¥–∏–µ–Ω—Ç */
    filter: drop-shadow(0 0 5px rgba(244, 67, 54, 0.5));
}

.progress-bar.medium {
    background: linear-gradient(135deg, #ff9800, #ffb300);
    /* –û—Ä–∞–Ω–∂–µ–≤—ã–π –≥—Ä–∞–¥–∏–µ–Ω—Ç */
    filter: drop-shadow(0 0 5px rgba(255, 152, 0, 0.5));
}

.progress-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #4e5183;
    /* –ë–µ–ª—ã–π —Ç–µ–∫—Å—Ç –¥–ª—è –∫–æ–Ω—Ç—Ä–∞—Å—Ç–∞ —Å —Ç–µ–º–Ω—ã–º —Ñ–æ–Ω–æ–º */
    font-size: 14px;
    /* –ß—É—Ç—å –±–æ–ª—å—à–µ –¥–ª—è —á–∏—Ç–∞–µ–º–æ—Å—Ç–∏ */
    font-weight: 600;
    /* –ü–æ–ª—É–∂–∏—Ä–Ω—ã–π —à—Ä–∏—Ñ—Ç */
    font-family: 'Inter', sans-serif;
    /* –ï–¥–∏–Ω—ã–π —à—Ä–∏—Ñ—Ç —Å –∫—Ä—É–≥–æ–≤—ã–º */
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.4);
    /* –¢–µ–Ω—å —Ç–µ–∫—Å—Ç–∞ */
    white-space: nowrap;
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.9);
    }

    to {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

.progress-bar-container {
    animation: fadeIn 0.5s ease-out forwards;
}