.circular-progress-container {
    position: relative;
    width: 140px;
    height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #1a1a1a;
    /* –¢–µ–º–Ω—ã–π —Ñ–æ–Ω –¥–ª—è –∫–æ–Ω—Ç—Ä–∞—Å—Ç–∞ */
    border-radius: 50%;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.5), inset 0 0 10px rgba(0, 0, 0, 0.3);
    /* –¢–µ–Ω—å —Å–Ω–∞—Ä—É–∂–∏ –∏ –≤–Ω—É—Ç—Ä–∏ */
    padding: 10px;
}

.circular-progress {
    transform: rotate(-90deg);
    /* –ü—Ä–æ–≥—Ä–µ—Å—Å –Ω–∞—á–∏–Ω–∞–µ—Ç—Å—è —Å–≤–µ—Ä—Ö—É */
}

.circular-progress-background {
    fill: none;
    stroke: #2c2c2c;
    /* –¢–µ–º–Ω–æ-—Å–µ—Ä—ã–π —Ñ–æ–Ω */
    stroke-width: 12;
    opacity: 0.8;
}

.circular-progress-bar {
    fill: none;
    stroke-linecap: round;
    /* –ó–∞–∫—Ä—É–≥–ª–µ–Ω–Ω—ã–µ –∫–æ–Ω—Ü—ã */
    stroke-width: 12;
    transition: stroke-dashoffset 0.6s ease-in-out, stroke 0.3s ease;
    /* –ü–ª–∞–≤–Ω–∞—è –∞–Ω–∏–º–∞—Ü–∏—è */
    filter: drop-shadow(0 0 5px rgba(76, 175, 80, 0.5));
    /* –°–≤–µ—á–µ–Ω–∏–µ –¥–ª—è –ø—Ä–æ–≥—Ä–µ—Å—Å–∞ */
}

.circular-progress-text {
    position: absolute;
    text-align: center;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    gap: 4px;
    font-family: 'Inter', sans-serif;
}

.progress-value {
    font-size: 24px;
    font-weight: 700;
    color: #ffffff;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
    /* –¢–µ–Ω—å —Ç–µ–∫—Å—Ç–∞ */
}

.progress-label {
    font-size: 12px;
    font-weight: 400;
    color: #c8cbcbf9;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.progress-percentage {
    font-size: 16px;
    font-weight: 600;
    color: #4caf50;
    /* –ó–µ–ª–µ–Ω—ã–π –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é */
    transition: color 0.3s ease;
}

/* –ò–∑–º–µ–Ω–µ–Ω–∏–µ —Ü–≤–µ—Ç–∞ –ø—Ä–æ—Ü–µ–Ω—Ç–∞ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è */
.circular-progress-container.low .progress-percentage {
    color: #f44336;
}

.circular-progress-container.medium .progress-percentage {
    color: #ff9800;
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

.circular-progress-container {
    animation: fadeIn 0.5s ease-out forwards;
}